ck=\"addoil\">设置油品信息</a>\n        </div>\n        <div class=\"quote-nav\">\n          <router-link :class=\"thCurId==item.id? 'cur':''\"\n                       v-for=\"item in threeAuthList\"\n                       :key=\"item.id\"\n                       :to=\"{path:item.action,query:{id:curId}}\">\n            {{item.name}}\n          </router-link>\n        </div>\n        <table>\n          <thead>\n            <tr>\n              <th>油品名称</th>\n              <th>图片</th>\n              <th>详情</th>\n              <th>修改</th>\n              <th>操作</th>\n            </tr>\n          </thead>\n          <tbody>\n            <tr v-for=\"item in list\">\n              <td class=\"sno\">{{item.name}}</td>\n              <td class=\"pic\">\n                <img :src=\"item.cover\"\n                     class=\"poimg\">\n              </td>\n              <td>\n                <a href=\"javascript:;\"\n                   @click=\"detail(item.id)\">查看</a>\n              </td>\n              <td class=\"edit\">\n                <a href=\"javascript:;\"\n                   @click=\"reset(item.id)\">修改</a>\n              </td>\n              <td class='del'>\n                <a href=\"javascript:;\"\n                   @click=\"del(item.id)\">删除</a>\n              </td>\n            </tr>\n          </tbody>\n        </table>\n        <div class=\"page_center\"\n             v-show=\"pageCount&&pageCount>1\">\n          <paging :page-count=\"pageCount\"\n                  :page=\"page\"\n                  @index=\"paging\"></paging>\n          <!--分页的组件-->\n        </div>\n      </div>\n    </div>\n    <div class=\"laybox\"\n         style=\"display:none\">\n      <ul>\n        <li>油品名称：{{obj.name}}</li>\n        <li>油品图片：<img :src=\"obj.cover\"\n               class=\"poimg\"></li>\n        <li>油品价格：{{obj.price}}</li>\n        <li>油品简介：<br>{{obj.about}}</li>\n      </ul>\n    </div>\n  </div>\n</template>\n<script type=\"text/ecmascript-6\">\nimport page from '../../common.js';\nexport default {\n  data () {\n    return {\n      list: [],\n      token: window.sessionStorage.getItem('bbytoken'),\n      obj: {},\n      reason: '',\n      reg_reason: '',\n      aid: '',\n      //分页数据\n      page: 1, //显示的页码数\n\n      pageCount: 1,\n      authList: [],\n      curId: 0,\n      seCurId: 0,\n      threeAuthList: \"\"\n    }\n  },\n  created () {\n    this.init();\n  },\n  watch: {\n    index () {\n      this.init();\n    }\n  },\n  methods: {\n\n    paging (e) {\n      this.page = e;\n      this.init()\n    },\n    init () {\n      this.$axios.post('admin/SystemSetup/oillist', {\n        token: this.token,\n        page: this.page\n      })\n        .then(res => {\n          if (res.data.code == 1) {\n            this.list = res.data.data.list;\n            this.pageCount = res.data.data.rows;\n          } else {\n            layer.msg(res.data.msg)\n          }\n        }).catch(err => { })\n    },\n    del: function (id) {\n      this.$axios.post('admin/SystemSetup/delOil', {\n        token: this.token,\n        id: id\n      })\n        .then(res => {\n\n          layer.msg(res.data.msg);\n          if (res.data.code == 1)\n            this.init();\n        }).catch(err => console.log(err))\n    },\n    detail: function (id) {\n      this.$axios.post('admin/SystemSetup/detail', {\n        token: this.token,\n        id: id\n      })\n        .then(res => {\n\n          this.obj = res.data.data;\n          if (this.obj.about)\n            this.obj.about = this.obj.about.replace(/<[^>]+>/g, \"\");\n        \n          layer.open({\n            type: 1,\n            area: ['50%', '60%'],\n            title: ['油品信息详情', 'height:60px;background-color:#98D1F9;color:white;text-align:center;line-height:60px;font-size:20px;'],\n            shadeClose: true,\n            shade: 0.8,\n            content: $('.laybox')\n          })\n        }).catch(err => console.log(err))\n    },\n    reset: function (id) {\n      var that = this;\n      layer.open({\n        type: 2,\n        area: ['80%', '80%'],\n        title: ['详情', 'height:60px;background-color:#98D1F9;color:white;text-align:center;font-size:20px;line-height:60px;'],\n        shadeClose: true,\n        shade: 0.8,\n        content: cartopiaURL + '/#/systemSet/saveoil?id=' + id,\n        end: function () {\n          that.$axios.post('admin/SystemSetup/oillist', {\n            token: that.token,\n            page: that.index\n          })\n            .then(res => {\n              that.list = res.data.data.list;\n              that.showpage(res.data.data);\n            })\n            .catch(err => console.log(err))\n        }\n      })\n    },\n    addoil: function () {\n      var that = this;\n      layer.open({\n        type: 2,\n        area: ['80%', '80%'],\n        title: ['详情', 'height:60px;background-color:#98D1F9;color:white;text-align:center;font-size:20px;line-height:60px;'],\n        shadeClose: true,\n        shade: 0.8,\n        content: cartopiaURL + '/#/systemSet/inclist',\n        //          content:'http://localhost:8081/#/systemSet/inclist',\n        end: function () {\n          that.$axios.post('admin/SystemSetup/oillist', {\n            token: that.token,\n            page: that.index\n          })\n            .then(res => {\n              that.list = res.data.data.list;\n              that.showpage(res.data.data);\n            })\n            .catch(err => console.log(err))\n        }\n      })\n    }\n  },\n  mounted () {\n    var id = this.$route.query.id;\n    this.curId = id;\n   \n    this.$axios.post('admin/Auth/erAuth', {\n      token: window.sessionStorage.getItem('bbytoken'),\n      id: id\n    })\n      .then(res => {\n\n        if (res.data.code == 1) {\n          var arr = res.data.data;\n          for (var i = 0; i < arr.length; i++) {\n            if (arr[i].name == '邦保养') {\n              this.seCurId = arr[i].id;\n            }\n         \n            if (arr[i].son) {\n              this.threeAuthList = arr[i].son\n              for (var j = 0; j < arr[i].son.length; j++) {\n                if (arr[i].action != arr[i].son[j].action) {\n                  arr[i].action = arr[i].son[0].action;\n                }\n\n                if (arr[i].son[j].name == '油品管理' && arr[i].name == '邦保养') {\n                  this.thCurId = arr[i].son[j].id;\n                \n                }\n\n              }\n            }\n          }\n          this.authList = arr;\n        } else {\n          this.$alert(res.data.msg, '提示', {\n            type: 'error'\n          });\n        }\n      })\n      .catch(err => {\n        alert(err);\n      })\n  }\n}\n</script>\n<style scoped>\n.head {\n  z-index: 80;\n}\n\ntable tr td {\n  padding: 0;\n  width: 20%;\n}\n\ntable th {\n  background-color: #fff;\n}\n\ntd {\n  height: 50px;\n}\n\n.poimg {\n  max-width: 150px;\n  max-height: 150px;\n}\n\n.edit a {\n  color: #00b4ff;\n}\n\n.del a {\n  color: #ff0000;\n}\n\nimg {\n  vertical-align: middle;\n}\n\n.quote-ele a {\n  width: 116px;\n  height: 36px;\n  border-radius: 18px;\n  color: white;\n  background-color: #00b4ff;\n  font-size: 16px;\n  float: right;\n  margin-right: 130px;\n  line-height: 35px;\n  text-align: center;\n  margin-top: 20px;\n}\n\na {\n  cursor: pointer;\n}\n\n.laybox {\n  padding: 40px 20px;\n  font-size: 16px;\n}\n\n.laybox li {\n  margin: 16px 0;\n}\n</style>\n"],"sourceRoot":""}]);

